.banner {
    height: 60vh;
    position: relative;
    background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen-28328347.jpg") center center;
}

.banner__information {
    background-color: black;
    color: white;
    padding-top: 30vh;
    padding-left: 50px;
    padding-right: 40px;
    padding-bottom: 25px;
    width: 300px;
}

.banner__search {
    display: flex;
    flex-direction: column;
}

.banner__searchButton {
    background-color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    color: #ff7779 !important;
}

.banner__information>button {
    background-color: #ff7779;
    color: white;
    text-transform: inherit;
    margin-top: 20px;
    font-weight: 600;
}

.banner__information>button:hover {
    background-color: white;
    color: #ff7779;
}

.banner__information>h5 {
    margin-top: 10px;
}